//n
export function spArray(N,Q){
  var R = [],F;
  for (F = 0;F < Q.length;) {
      R.push(Q.slice(F,F += N))
  }
  return R
}

//
export function goBack(index, url) {
	if (index == 1) {
		// 
		uni.navigateBack({
			delta: url,
			animationType: 'pop-out',
			animationDuration: 300
		});
	} else if (index == 2) {
		// tabbaruni.navigateBack
		uni.navigateTo({
			url: url,
			animationType: 'pop-in',
			animationDuration: 300
		})
	} else if (index == 3) {
		//  tabBar  tabBar 
		uni.switchTab({
			url: url,
			animationType: 'pop-in',
			animationDuration: 300
		})
	} else if (index == 4) {
		// 
		uni.reLaunch({
			url: url,
			animationType: 'pop-in',
			animationDuration: 300
		})
	} else if (index == 5) {
		// 
		uni.redirectTo({
			url: url,
		});
	}
}

