// 1
export function groupBy(paper,key){
	const groupedBy = {}
	for (const item of paper) {
		if (groupedBy[item[key]]) {
			groupedBy[item[key]].push(item)
		} else {
			groupedBy[item[key]] = [item]
		}
	}
	return groupedBy
}
