// 
export function numToCapital(num, type = ''){
  if (!num) return 0
  const strNum = Number((num + '').replace(/[,]*/g, '')) + '' // 
  num = parseInt(Number(strNum)) // 
  let capitalAr = ''
  let unitAr = ['', '', '', '', '', '', '', '', '', '', '']
  if (type) {
    capitalAr = ''
    unitAr = ['', '', '', '', '', '', '', '', '', '', ''] // 
  }
  const resultAr = [] // json.in
  let index = strNum.length - 1 //
  let idx = 0 // 
  let percent = 10
  const turnNum = (num, percent, index) => {
    const unit = num / percent
    const capital = capitalAr[Number(strNum[index])]
    if (unit < 1) {
      resultAr.push(capital)
      // 11
      if (Number(strNum[index]) === 1 && (strNum.length === 2 || strNum.length === 6 || strNum.length === 10)) {
        resultAr.pop()
      }
      return false //
    } else {
      if (capital === '') {
        // 
        if (!['', ''].includes(resultAr[resultAr.length - 1])) {
          resultAr.pop()
        }
        // 
        if (resultAr[resultAr.length - 1] === '') {
          resultAr.pop()
        }
      }
      resultAr.push(capital)
      // 
      if (['', ''].includes(resultAr[resultAr.length - 2]) && capital === '') {
        resultAr.pop()
      }
      // 1
      if (resultAr[0] === '' && resultAr[1] === '') {
        resultAr.shift()
      }
      // 
      if (resultAr[0] === '') {
        resultAr.pop()
      }
      resultAr.push(unitAr[idx++])
      turnNum(num, percent * 10, --index)
    }
  }
  turnNum(num, percent, index)
  return resultAr.reverse().join('')
}
