import Vue from "vue"
import Vuex from "vuex"
 
Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        userId:'',
        userInfo: {},  
        hasLogin: false,
        question_set:[],
        pathName: "",
        currDbSource: {},
        currJobData: {},
        DbSource: []
    },
    mutations:{
    getQuestionset(state,e){
        state.question_set =e
    },
    // getId(state,id){
    //         state.userId =id
    //     },
    login(state, provider) {//  
        state.hasLogin = true  
        // state.uerInfo.token = provider.token  
        state.userInfo.phone = provider.phone
        state.userInfo.password = provider.password
        provider = JSON.stringify(provider);
        uni.setStorage({//  
            key: 'userInfo',  
            data: provider  
        })  
    },  
    logout(state) {//  
        state.hasLogin = false  
        state.userInfo = {}  
        uni.removeStorage({
            key:'id'
        })  
        uni.removeStorage({
            key:'userInfo'
        })  
      },

        // 
        savePath(state,pathName){
            state.pathName = pathName;
        },
        // 
        saveCurrDbSource(state,currDbSource){
            state.currDbSource = currDbSource;
        },
        // 
        saveCurrJobData(state,currJobData){
            state.currJobData = currJobData;
        },
        // 
        saveDbSource(state,DbSource){
            state.DbSource = DbSource;
        }
    }
})